body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: #eee;
}

.showcase{
  background:url('https://images.pexels.com/photos/3953481/pexels-photo-3953481.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500') no-repeat right/cover;
  height: 400px;

}
.showcase .overlay{
  background-color: rgba(0, 0, 0, 0.4);
  height: 400px;
  display: flex;
  flex-direction:column ;
  align-items: center;
  justify-content: center;
}

@media(min-width : 1024px){
  .showcase{
    background:url('https://images.pexels.com/photos/3953481/pexels-photo-3953481.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500') no-repeat top/cover;
  height: 400px;

  }
}